html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  box-sizing: border-box;
  --black: #000;
  --active-color: #00acdc;
  margin: 0;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.logo_link {
  height: 40px;
}

.navigation {
  margin: 0;
}

.nav__links {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.link {
  text-transform: uppercase;
  color: var(--black);
  height: 60px;
  margin-right: 20px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__links li:last-child a {
  margin-right: 0;
}

.link:hover, .is-active {
  color: var(--active-color);
}

.is-active:after {
  content: url("active_line.1dbca468.svg");
  position: absolute;
  bottom: -26px;
  left: 0;
}
